            <b-form>
              <b-row>
                <b-col cols="12" md="12">
                  <b-form-group>
                    <h5 class="font-weight-bold"></h5>
                    <validation-provider
                      #default="{ errors }"
                      name=""
                      rules="required|"
                    >
                      <b-form-input
                        id="installment"
                        type="text"
                        v-model="form.promotion_name"
                      />
                      <small class="text-danger">{{ errors[0] }}</small>
                    </validation-provider>
                  </b-form-group>
                </b-col>
                <b-col cols="4" md="2">
                  <b-form-group>
                    <h5 class="font-weight-bold"> 2  (%)</h5>
                    <validation-provider
                      #default="{ errors }"
                      name=" 2 "
                      rules="required|"
                    >
                      <b-form-input
                        id="installment"
                        type="number"
                        v-model="form.discount_2top"
                      />
                      <small class="text-danger">{{ errors[0] }}</small>
                    </validation-provider>
                  </b-form-group>
                </b-col>
                <b-col cols="4" md="2">
                  <b-form-group>
                    <h5 class="font-weight-bold"> 2  (%)</h5>
                    <validation-provider
                      #default="{ errors }"
                      name=" 2 "
                      rules="required|"
                    >
                      <b-form-input
                        id="installment"
                        type="number"
                        v-model="form.discount_2bottom"
                      />
                      <small class="text-danger">{{ errors[0] }}</small>
                    </validation-provider>
                  </b-form-group>
                </b-col>
                <b-col cols="4" md="2">
                  <b-form-group>
                    <h5 class="font-weight-bold"> 3  (%)</h5>
                    <validation-provider
                      #default="{ errors }"
                      name=" 3 "
                      rules="required|"
                    >
                      <b-form-input
                        id="installment"
                        type="number"
                        v-model="form.discount_3top"
                      />
                      <small class="text-danger">{{ errors[0] }}</small>
                    </validation-provider>
                  </b-form-group>
                </b-col>
                <b-col cols="4" md="2">
                  <b-form-group>
                    <h5 class="font-weight-bold"> 3  (%)</h5>
                    <validation-provider
                      #default="{ errors }"
                      name=" 3 "
                      rules="required|"
                    >
                      <b-form-input
                        id="installment"
                        type="number"
                        v-model="form.discount_3tod"
                      />
                      <small class="text-danger">{{ errors[0] }}</small>
                    </validation-provider>
                  </b-form-group>
                </b-col>
                <b-col cols="4" md="2">
                  <b-form-group>
                    <h5 class="font-weight-bold">  (%)</h5>
                    <validation-provider
                      #default="{ errors }"
                      name=" "
                      rules="required|"
                    >
                      <b-form-input
                        id="installment"
                        type="number"
                        v-model="form.discount_runtop"
                      />
                      <small class="text-danger">{{ errors[0] }}</small>
                    </validation-provider>
                  </b-form-group>
                </b-col>
                <b-col cols="4" md="2">
                  <b-form-group>
                    <h5 class="font-weight-bold">  (%)</h5>
                    <validation-provider
                      #default="{ errors }"
                      name=" "
                      rules="required|"
                    >
                      <b-form-input
                        id="installment"
                        type="number"
                        v-model="form.discount_runbottom"
                      />
                      <small class="text-danger">{{ errors[0] }}</small>
                    </validation-provider>
                  </b-form-group>
                </b-col>

                <!-- pay rate -->
                <b-col cols="4" md="2">
                  <b-form-group>
                    <h5 class="font-weight-bold"> 2  ()</h5>
                    <validation-provider
                      #default="{ errors }"
                      name=" 2 "
                      rules="required|"
                    >
                      <b-form-input
                        id="installment"
                        type="number"
                        v-model="form.pay_2top"
                      />
                      <small class="text-danger">{{ errors[0] }}</small>
                    </validation-provider>
                  </b-form-group>
                </b-col>
                <b-col cols="4" md="2">
                  <b-form-group>
                    <h5 class="font-weight-bold"> 2  ()</h5>
                    <validation-provider
                      #default="{ errors }"
                      name=" 2 "
                      rules="required|"
                    >
                      <b-form-input
                        id="installment"
                        type="number"
                        v-model="form.pay_2bottom"
                      />
                      <small class="text-danger">{{ errors[0] }}</small>
                    </validation-provider>
                  </b-form-group>
                </b-col>
                <b-col cols="4" md="2">
                  <b-form-group>
                    <h5 class="font-weight-bold"> 3  ()</h5>
                    <validation-provider
                      #default="{ errors }"
                      name=" 3 "
                      rules="required|"
                    >
                      <b-form-input
                        id="installment"
                        type="number"
                        v-model="form.pay_3top"
                      />
                      <small class="text-danger">{{ errors[0] }}</small>
                    </validation-provider>
                  </b-form-group>
                </b-col>
                <b-col cols="4" md="2">
                  <b-form-group>
                    <h5 class="font-weight-bold"> 3  ()</h5>
                    <validation-provider
                      #default="{ errors }"
                      name=" 3 "
                      rules="required|"
                    >
                      <b-form-input
                        id="installment"
                        type="number"
                        v-model="form.pay_3tod"
                      />
                      <small class="text-danger">{{ errors[0] }}</small>
                    </validation-provider>
                  </b-form-group>
                </b-col>
                <b-col cols="4" md="2">
                  <b-form-group>
                    <h5 class="font-weight-bold">  ()</h5>
                    <validation-provider
                      #default="{ errors }"
                      name=" "
                      rules="required|"
                    >
                      <b-form-input
                        id="installment"
                        type="number"
                        v-model="form.pay_runtop"
                      />
                      <small class="text-danger">{{ errors[0] }}</small>
                    </validation-provider>
                  </b-form-group>
                </b-col>
                <b-col cols="4" md="2">
                  <b-form-group>
                    <h5 class="font-weight-bold">  ()</h5>
                    <validation-provider
                      #default="{ errors }"
                      name=" "
                      rules="required|"
                    >
                      <b-form-input
                        id="installment"
                        type="number"
                        v-model="form.pay_runbottom"
                      />
                      <small class="text-danger">{{ errors[0] }}</small>
                    </validation-provider>
                  </b-form-group>
                </b-col>
                <!-- submit and reset -->
                <b-col>
                  <b-button
                    v-ripple.400="'rgba(255, 255, 255, 0.15)'"
                    type="submit"
                    variant="primary"
                    class="mr-1"
                    @click.prevent="validationForm"
                  >
                    
                  </b-button>
                </b-col>
              </b-row>
            </b-form>
          </validation-observer>
        </b-card-code>
      </b-col>
    </b-row>
  </section>
</template>

<script>
import BCardCode from "@core/components/b-card-code";
import ToastificationContent from "@core/components/toastification/ToastificationContent.vue";
import {
  BRow,
  BCol,
  BFormGroup,
  BFormInput,
  BFormCheckbox,
  BForm,
  BButton,
  BFormFile,
} from "bootstrap-vue";
import { mapGetters } from "vuex";
import Ripple from "vue-ripple-directive";
import flatPickr from "vue-flatpickr-component";
import moment from "moment";
import vSelect from "vue-select";
import { ValidationProvider, ValidationObserver } from "vee-validate";
import { required, integer } from "@validations";
import { HTTP } from "@/services/http-common";

export default {
  components: {
    BCardCode,
    BRow,
    BCol,
    BFormGroup,
    BFormInput,
    BFormCheckbox,
    BForm,
    BButton,
    BFormFile,
    flatPickr,
    vSelect,
    // validations
    ValidationProvider,
    ValidationObserver,
  },
  directives: {
    Ripple,
  },
  data() {
    return {
      option: [],
      // validation
      required,
      integer,
      form: {
        promotion_name: "",
        discount_2top: "",
        discount_2bottom: "",
        discount_3top: "",
        discount_3tod: "",
        discount_runtop: "",
        discount_runbottom: "",
        pay_2top: "",
        pay_2bottom: "",
        pay_3top: "",
        pay_3tod: "",
        pay_runtop: "",
        pay_runbottom: "",
      },
      url: "",
    };
  },
  mounted() {
    this.getData();
  },
  methods: {
    getData() {
      this.isLoading = true;
      HTTP.get("api/type")
        .then((response) => {
          this.option = response.data.data;
        })
        .catch((error) => {
          this.$toast({
            component: ToastificationContent,
            position: "top-right",
            props: {
              title: "",
              text:
                error.response.data.msg !== undefined
                  ? error.response.data.msg
                  : error.status,
              icon: "XCircleIcon",
              variant: "danger",
            },
          });
        });
    },
    validationForm() {
      this.$refs.addPromotion.validate().then((success) => {
        if (success) {
          //   let installment = parseInt(this.form.installment);
          //   let set = parseInt(this.form.set);
          //   let lotterNumber = parseInt(this.form.lotteryNumber);
          let formData = new FormData();
          formData.append("promotion_name", this.form.promotion_name);
          formData.append("discount_2top", this.form.discount_2top);
          formData.append("discount_2bottom", this.form.discount_2bottom);
          formData.append("discount_3top", this.form.discount_3top);
          formData.append("discount_3tod", this.form.discount_3tod);
          formData.append("discount_runtop", this.form.discount_runtop);
          formData.append("discount_runbottom", this.form.discount_runbottom);
          formData.append("pay_2top", this.form.pay_2top);
          formData.append("pay_2bottom", this.form.pay_2bottom);
          formData.append("pay_3top", this.form.pay_3top);
          formData.append("pay_3tod", this.form.pay_3tod);
          formData.append("pay_runtop", this.form.pay_runtop);
          formData.append("pay_runbottom", this.form.pay_runbottom);

          HTTP.post(`api/promotion/add-promotion`, {
            promotion_name: this.form.promotion_name,
            discount_2top: this.form.discount_2top,
            discount_2bottom: this.form.discount_2bottom,
            discount_3top: this.form.discount_3top,
            discount_3tod: this.form.discount_3tod,
            discount_runtop: this.form.discount_runtop,
            discount_runbottom: this.form.discount_runbottom,
            pay_2top: this.form.pay_2top,
            pay_2bottom: this.form.pay_2bottom,
            pay_3top: this.form.pay_3top,
            pay_3tod: this.form.pay_3tod,
            pay_runtop: this.form.pay_runtop,
            pay_runbottom: this.form.pay_runbottom,
          })
            .then((resp) => {
              if (
                resp.data.status != undefined &&
                resp.data.status === "error"
              ) {
                this.showToast(
                  resp.data.message != undefined
                    ? resp.data.message
                    : " ",
                  "danger"
                );
              } else {
                this.showToast("", "success");
                this.$router.go(-1);
              }
            })
            .catch((err) => {
              if (err.response) {
                if (err.response.status === 403) {
                  this.$store.dispatch("logoutAuth");
                  this.$router.push("/login");
                } else {
                  this.showToast(
                    err.response.data.msg !== undefined
                      ? err.response.data.msg
                      : " ",
                    "danger"
                  );
                }
              }
            });
        }
      });
    },
    onFileChange(files) {
      const file = files.target.files[0];
      if (file) {
        this.url = URL.createObjectURL(file);
      }
      this.form.lotteryImg = file;
    },
    showToast(text, variant) {
      this.$toast({
        component: ToastificationContent,
        position: "top-right",
        props: {
          title: "...",
          text: text,
          icon: variant === "success" ? "CheckIcon" : "BellIcon",
          variant: variant,
        },
      });
    },
  },
  computed: {
    ...mapGetters(["User"]),
  },
};
</script>

<style lang="scss">
@import "@core/scss/vue/pages/dashboard-ecommerce.scss";
@import "@core/scss/vue/libs/chart-apex.scss";
@import "@core/scss/vue/libs/vue-flatpicker.scss";
@import "@core/scss/vue/libs/vue-select.scss";
</style>
